}
.header-bottom nav > ul:first-child > li {
  position: relative;
}
.header-bottom nav > ul:first-child > li.active::after {
  height: 8px;
  width: 100%;
  visibility: visible;
  opacity: 1;
}
.header-bottom nav > ul:first-child > li::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0;
  width: 0;
  visibility: hidden;
  opacity: 0;
  background-color: #CDF3F9;
  border-radius: 4px 4px 0px 0px;
  transition: width 0.5s ease-in-out;
  z-index: 1;
}
.header-bottom nav > ul:first-child > li:hover::after {
  height: 8px;
  width: 100%;
  visibility: visible;
  opacity: 1;
}
.header-bottom nav > ul:first-child > li > a {
  display: inline-block;
  height: 60px;
  display: flex;
  align-items: center;
}
.header-bottom nav > ul:first-child li:hover a svg {
