  height: 16px;
  flex-shrink: 0;
  margin-left: 6px;
  transition: transform 0.5s;
}
.header-bottom nav > ul:first-child li ul {
  background-color: #fff;
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 12px;
  box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
}
