  margin-left: 16px;
}
.header-bottom nav > ul:first-child li ul li:hover .header-bottom-list {
  display: block;
}
.header-bottom nav > ul:first-child li ul li:hover .header-bottom-list ul {
  display: block;
}
.header-bottom nav > ul:first-child li ul li:hover a svg {
  opacity: 0;
  transform: none;
}
.header-bottom nav > ul:first-child li ul li ul {
  display: none;
  padding-right: 10px;
  padding-top: 0;
  padding-bottom: 0;
  max-height: 420px;
  overflow-y: auto;
  border-radius: 0;
  box-shadow: none;
}
